.Menu__navigation {
    &-list {
        margin: 0 auto;
        padding: 24px 0;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        text-align: center;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
        &-item {
            font-family: Open Sans;
            font-style: normal;
            font-weight: bold;
            font-size: 10px;
            line-height: 14px;
            text-align: center;
            text-transform: uppercase;
            padding: 4px;
            &:not(:last-child) {
                border-right: 1px solid #33333333;
            }
            &-link {
                color: #333333;
                cursor: pointer;
            }
            &-active {
                color: #333333;
                cursor: pointer;
                position: relative;
                &::after {
                    position: absolute;
                    content: '';
                    width: 75%;
                    height: 4px;
                    background-color: #e8c300;
                    border-radius: 46px;
                    top: 50%;
                    left: 50%;
                    margin-right: -50%;
                    -webkit-transform: translate(-50%, -50%);
                    -ms-transform: translate(-50%, -50%);
                    transform: translate(-50%, -50%);
                    margin-top: 16px;
                }
            }
        }
    }
}

@media (min-width: 768px) {
    .Menu__navigation {
        &-list {
            padding: 48px 0;
            &-item {
                padding: 16px 29px;
                font-size: 12px;
                line-height: 17px;
            }
        }
    }
}

@media (min-width: 1024px) {
    .Menu__navigation {
        &-list {
            &-item {
                padding: 16px 44px;
            }
        }
    }
}

@media (min-width: 1280px) {
    .Menu__navigation {
        &-list {
            &-item {
                padding: 16px 60px;
            }
        }
    }
}

@media (min-width: 1600px) {
    .Menu__navigation {
        &-list {
            &-item {
                padding: 16px 80px;
            }
        }
    }
}