.Menu {
    width: 280px;
    margin: 0 auto;
    &__header {
        position: relative;
        text-align: center;
        font-family: 'Banny Bold Round';
        font-size: 34px;
        line-height: 40px;
        text-transform: uppercase;
        margin: 30px 0;
        padding-bottom: 30px;
        &::after {
            position: absolute;
            content: '';
            width: 56px;
            height: 4px;
            background-color: #e8c300;
            border-radius: 46px;
            top: 50%;
            left: 50%;
            margin-right: -50%;
            -webkit-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            margin-top: 40px;
        }
    }
    &__description {
        font-family: Open Sans;
        font-style: normal;
        font-weight: bold;
        font-size: 17px;
        line-height: 26px;
        text-align: center;
        color: #000000;
    }
    &__list {
        font-family: 'Banny Bold Round';
        font-size: 17px;
        line-height: 17px;
        color: #333333;
    }
}

@media (min-width: 375px) {
    .Menu {
        width: 336px;
    }
}

@media (min-width: 768px) {
    .Menu {
        width: 680px;
        &__header {
            line-height: 45px;
        }
        &__list {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        }
    }
}

@media (min-width: 1024px) {
    .Menu {
        width: 912px;
        margin-bottom: 40px;
    }
}

@media (min-width: 1280px) {
    .Menu {
        width: 1140px;
    }
}

@media (min-width: 1600px) {
    .Menu {
        width: 1424px;
    }
}