.Mobile {
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    background-color: #000000a3;
    height: 100%;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    left: 0;
    overflow-y: auto;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 20;
    &__navigation {
        position: relative;
        overflow: hidden;
        z-index: 25;
        padding-top: 32px;
        background-color: #333333;
        &-list {
            margin: 32px 0;
            &-item {
                font-family: Open Sans;
                font-style: normal;
                font-weight: bold;
                font-size: 12px;
                line-height: 17px;
                text-transform: uppercase;
                padding: 8px;
                &-link {
                    text-decoration: none;
                    color: #fff;
                }
            }
        }
        &-close {
            display: none;
        }
        &-container {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-pack: justify;
            -ms-flex-pack: justify;
            justify-content: space-between;
            width: 316px;
            margin: 0 auto;
        }
    }
}

@media (min-width: 375px) {
    .Mobile {
        &__navigation {
            padding-top: 48px;
            &-container {
                width: 316px;
            }
        }
    }
}

@media (min-width: 768px) {
    .Mobile {
        &__navigation {
            padding-top: 48px;
            &-list {
                margin: 32px auto;
                text-align: center;
            }
            &-container {
                width: 660px;
            }
        }
    }
}