.Control {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    text-align: center;
    margin: 0 auto;
    width: 100px;
    &__item {
        margin: 12px;
        &-button {
            width: 10px;
            height: 10px;
            border-radius: 50%;
            border: none;
            outline: none;
            background-color: #ffffff80;
            &:hover,
            &:focus,
            &:active {
                background-color: #fff;
            }
        }
    }
}

@media (min-width: 1024px) {
    .Control {
        margin: 0 auto;
        padding-bottom: 30px;
    }
}